import React, { useState } from 'react';
import './FilePages/Styles.css';
import {Container, Nav, NavDropdown} from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import HomePage from './FilePages/HomePage';
import ExampleRates from './FilePages/ExampleRates';
import Contact from './FilePages/Contact';
import Deposits from './FilePages/PaymentPage';
import Portfolio from './FilePages/Portfolio';
import { BrowserRouter,  Router, Route, Link, NavLink } from 'react-router-dom';
import Navbar from 'react-bootstrap/Navbar'

const routes = [
  { path: '/', name: 'Home', Component: HomePage },
  { path: '/ExampleRates', name: 'About', Component: ExampleRates },
  { path: '/Contact', name: 'Contact', Component: Contact },
  { path: '/PaymentPage', name: 'Deposits', Component: Deposits },
]


function App() {
  const [expanded, setExpanded] = useState(false);
  return (

    <div>
    <div className="NaviStyle">
    <BrowserRouter>
    <Navbar collapseOnSelect  expand="lg"expanded={expanded} bg="dark" variant="dark">
    <Navbar.Toggle onClick={() => setExpanded(expanded ? false : "expanded")} />
  <Navbar.Collapse id="responsive-navbar-nav">
  <Nav className="mr-auto"  >
            {routes.map(route => (
            
              <Nav.Link
              onClick={() => setExpanded(false)}

                key={route.path}
                as={Link}
               
                
                to={route.path}
                activeClassName="active"
                exact
                
              >
                
                {route.name}
                
              </Nav.Link>
            ))}
            
            <NavDropdown title="Portfolio" id="collasible-nav-dropdown">
        <NavDropdown.Item href="https://www.homeraisedcairns.com/"> Home Raised Cairns - AKC Puppies</NavDropdown.Item>
        <NavDropdown.Divider />
        <NavDropdown.Item href="https://github.com/KrotzerSoftwareDevelopment/"> GitHub </NavDropdown.Item>
      </NavDropdown>
          </Nav>
   
    <Nav>
    <NavDropdown.Divider />
      <Nav.Link eventKey={2} href="https://www.linkedin.com/in/dean-krotzer-0800b11a2/">
        LinkedIn
      </Nav.Link>
    </Nav>
  </Navbar.Collapse>

</Navbar>
<Container className="container">
          {routes.map(({ path, Component }) => (
            <Route key={path} exact path={path}>
              {({ match }) => (
                <CSSTransition
                
                  in={match != null}
                  timeout={300}
                  classNames="page"
                  unmountOnExit
                >
                  <div className="page">
                    <Component />
                  </div>
                </CSSTransition>
              )}
            </Route>
          ))}
        </Container>
    <div className="grid-container">
      

</div>
</BrowserRouter>
</div>
</div>
  );
}

export default App;
